// COLORS
// ------------------------------
$primary-color: #335eea;
$menu-item-color: #52596F;
$primary-link-color: #52596F;
$text-link-color: #52596F;
$text-primary-color: #2a2b31;
$text-primary-blue: #1C84E3;

// BUTTOONS
$btn-primary-color: #fff;
$outline-btn-color: #C7CDE3;
$btn-sec-bg: rgba(153, 212, 255, 0.3);
$btn-sec-bg-hover: rgba(153, 212, 255, 0.45);
$btn-sec-bg-active: rgba(153, 212, 255, 0.6);
$btn-sec-color: #46A7ED;
$header-phone-color: #52596F;
$header-mail-color: #9AA2BA;
$top-menu-toggler-border: #C7CDE3;


// Modal Variables
// ------------------------------
$modal-trigger-bg: #fa7c30;
$modal-content-bg: #fff;
$modal-close-item-bg: #7D859D;
$modal-close-item-bg-hover: #161418;
$modal-overlay-bg: rgba(42, 43, 49, 0.2);
$modal-border-radius: 24px;
$modal-border-radius-sm: 16px;

// Form Variables
// ------------------------------
$form-control-radius: 1rem;
$form-control-color: #2A2B31;
$form-control-bg: #fff;
$form-control-border: #E8EAF4;
$form-control-border-hover: #DCDEEE;
$form-control-border-focus: #46A7ED;
$form-control-shadow: rgba(125, 133, 157, 0.1);
$form-label-color: #7D859D;
$form-label-padding-left: 5.8rem;

// Swiper-slider Variables
// ------------------------------
$swiper-btn-bg: linear-gradient(180deg, #37AAFD 0%, #1C84E3 100%);
$swiper-btn-hover-bg: linear-gradient(180deg, #54B7FF 0%, #2A93F4 100%);
$swiper-btn-active-bg: #5BB8FF;


// Stars-slider Variables
// ------------------------------
$stars-slide-width: 75rem;
$stars-card-bg: #ffffff;
$stars-card-rad: 2rem;
$stars-card-inner-rad: 1.6rem;
$stars-slider-btn-width: 5rem;


// Reviews-slider Variables
// ------------------------------
$reviews-slide-width: 30.2rem;


// About Variables
// ------------------------------
$grid-item-rad: 1.6rem;

// Overviews Variables
// ------------------------------
$main-video-width: 85rem;
$main-video-height: 47rem;
$main-video-m-b: 6rem;

// Partnership Variables
// ------------------------------
$content-right-offset-y: 26rem;

// Footer Variables
// ------------------------------
$footer-color: #52596F;



// Grid Variables
// ------------------------------
$gutter: 1.5rem;

$column1: calc(100% / 12);
$column2: calc(100% / 6);
$column3: calc(100% / 4);
$column4: calc(100% / 3);
$column5: calc(100% / 12 * 5);
$column6: calc(100% / 2);
$column7: calc(100% / 12 * 7);
$column8: calc(100% / 3 * 2);
$column9: calc(100% / 4 * 3);
$column10: calc(100% / 6 * 5);
$column11: calc(100% / 12 * 11);
$column12: 100%;