/* II. Styles reset
--------------------------------------- */ 

*,
*:before,
*:after{
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	-webkit-tap-highlight-color: transparent;
}

html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
   border: 0;
   outline: 0;
//    font-size: 100%;
   vertical-align: baseline;
   background: transparent;
}

[hidden] { display: none; }

button,
input{
	border: none;
	outline: none;
	border-radius: 0;
}

code, kbd, pre, samp {
	font-family: monospace, serif;
	font-size: 1em;
 }

i{display: inline-block;}
 
pre {
	white-space: pre;
	white-space: pre-wrap;
	word-wrap: break-word;
}
 
blockquote,
q{
	quotes: &#8220 &#8220;
	&:before,
	&:after{
		content: '';
		content: none;
	}
}

ul, ol{
	list-style: none;
}
 
table {
	border-collapse: collapse;
	border-spacing: 0;
}


/* III. Default styles
--------------------------------------- */ 

/* 1. Basic
------------------- */ 

html{
	font-family: 'Open Sans', "Helvetica", sans-serif;
	font-size: 10px;
	font-weight: 400;
	line-height: 1.17;
	letter-spacing: .05em;
}


h1, h2, h3, h4, h5, h6,
p{
	cursor: default;
}

// Use .container if grid.css is not connected
.container{
	&,
	&_xl,
	&_lg,
	&_md,
	&_sm{
		margin: 0 auto;
		padding: 0 v.$gutter;
	}
}

.container{
	max-width: 123rem;
	&_xl{
		max-width: 114rem;
	}
	&_lg{
		max-width: 96rem;
	}
	&_md{
		max-width: 72rem;
	}
	&_sm{
		max-width: 54rem;
	}
}

.d{
	&-none{
		display: none;
	}
	&-block{
		display: block;
	}
	&-inline{
		display: inline;
	}
}

.scroll-hidden{
  /*для скрытия полосы прокрутки*/
  padding-right: 17px;
  overflow: hidden;
}

.wrapper{
	margin: 0 auto;
	max-width: 1920px;
	overflow: hidden;
	position: relative;
}
.main{
	flex: 1 auto;
}

/* 2. Typography
-------------------------*/
