	&_primary-blue{
		color: v.$text-primary-blue;
	}
	&_medium{
		font-weight: 600;
	}
	&_semibold{
		font-weight: 600;
	}
	&_bold{
		font-weight: 700;
	}
}

/* 3. Links
-------------------------*/

a, a:visited {
   text-decoration: none;
   outline: none;
   color: v.$menu-item-color;
   transition: color .3s ease-in-out;
   &.text-link{
	color: v.$text-link-color;
	text-decoration: underline;
   }
}
a{
	&.text-link{
		&:hover,
		&:focus{
			text-decoration: none;
		}
		&:active{
			text-decoration: none;
		}
	}
}


 /* 4. Images
 -------------------------*/

 img {
	display: block;
	max-width: 100%;
	height: auto;
 }

